import React, { useState } from "react";
import PromptBuilder from "./PromptBuilder";
import buttonData from "./buttonData";
import buttonDataEN from "./buttonDataEN";
import buttonDataDummy1 from "./buttonDataDummy1";
import buttonDataDummy2 from "./buttonDataDummy2";
import { AppBar, Toolbar, Typography, IconButton, Drawer, Box } from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import HomeIcon from "@mui/icons-material/Home";
import StarIcon from "@mui/icons-material/Star";
import SettingsIcon from "@mui/icons-material/Settings";
import "./Prompt.css";

function App() {
    const [currentData, setCurrentData] = useState({ buttonData, buttonDataEN });
    const [drawerOpen, setDrawerOpen] = useState(false);

    const handleDataSetChange = (dataSet) => {
        setCurrentData(dataSet);
        setDrawerOpen(false);
    };

    

    const toggleDrawer = () => {
        setDrawerOpen(!drawerOpen);
    };

    
    const renderMenuItem = (icon, text, dataSet) => (
        <Box
          onClick={() => handleDataSetChange(dataSet)}
          className={`menu-item ${drawerOpen ? "menu-item-open" : "menu-item-closed"}`}
        >
          <Box className="menu-item-icon">{icon}</Box>
          <Typography className="menu-item-text">{text}</Typography>
        </Box>
      );
      
    

    return (
        <Box className="app">
            <AppBar position="fixed" className="appbar custom-appbar">
                <Toolbar>
                    <IconButton className="menu-button" edge="start" color="inherit" onClick={toggleDrawer}>
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                        Dashboard
                    </Typography>
                </Toolbar>
            </AppBar>
            <Drawer open={drawerOpen} onClose={toggleDrawer}>
                {renderMenuItem(<HomeIcon />, "Original Data Set", { buttonData, buttonDataEN })}
                {renderMenuItem(<StarIcon />, "Dummy Data Set 1", { buttonData: buttonDataDummy1, buttonDataEN: buttonDataDummy1 })}
                {renderMenuItem(<SettingsIcon />, "Dummy Data Set 2", { buttonData: buttonDataDummy2, buttonDataEN: buttonDataDummy2 })}
            </Drawer>
            <Box component="main" className="app-container">
                <PromptBuilder buttonData={currentData.buttonData} buttonDataEN={currentData.buttonDataEN} />
            </Box>
        </Box>
    );
}

export default App;
